import React, { lazy, Suspense } from 'react';
import { Routes, Route } from 'react-router-dom';
import {PagePreloader} from '../../components/preloader';

const HomePage = lazy(() => import('../../pages/HomePage'));
const Authe = lazy(() => import('../../pages/Authe'));
// const Pricing = lazy(() => import('../../pages/LandingPage/Pricing'));
// const Terms = lazy(() => import('../../pages/LandingPage/Terms'));
// const Policy = lazy(() => import('../../pages/LandingPage/Policy'));


export default function Authetications() {
    return (
        <Suspense fallback={<PagePreloader />}>
            <Routes>
                <Route path="/">
                    <Route index element={<HomePage />} />
                    <Route path="authe/:pageName" element={<Authe />} />
                    {/* <Route path="login" element={<Authe />} />
                    <Route path="authe/:pageName" element={<Authe />} />
                    <Route path="pricing" element={<Pricing />} />
                    <Route path="terms-and-conditions" element={<Terms />} />
                    <Route path="policy" element={<Policy />} /> */}
                </Route>
            </Routes>
        </Suspense>
    );
}


export const AutheticationPageLists = [
    '/',
    'process',
    'login',
    'authe',
    'pricing',
    'verify-email',
    'sign-up',
    'forgot-password',
    'terms-and-conditions',
    'policy',
    'reset-password',
    'profile-verification-page',
    'not-found',
];
