import React, { useState, useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import useUserDetails from '@components/Hooks/useUserDetails';
import SojiBaseInit from '@modules/SojiBase';
// Components

// Routes
import Authetications, { AutheticationPageLists } from './modules/Authetications';
import Access from './modules/Access';

const Routers = () => {
    const location = useLocation();
    const [isGeneralRoutes, setGeneralRoutes] = useState(true);
    const [page, setPage] = useState('Dashboard');
    const useUserDetail = useUserDetails()


    useEffect(() => {
        const arraySet = location.pathname.split('/');
        setPage(arraySet[1]);
        if (AutheticationPageLists.includes(location.pathname) || AutheticationPageLists.includes(arraySet[1])) {
            setGeneralRoutes(true);
        } else {
            setGeneralRoutes(false);
        }
    }, [location]);

    useEffect(() => {
        (async () => {
            const SojiBase = await SojiBaseInit;
            const getter = await SojiBase.get('v1/users/user_details');
            getter.on('static', (response) => {
                if(response.response.status != 406){
                    useUserDetail.set(response.response.data[0])
                }
            })
        })()
    },[])

    return <>{isGeneralRoutes ? <Authetications /> : <Access />}</>;
};

export default Routers;
