import React from 'react';
import './index.css';
import Soji from '@components/assets/Logo/soji_2.png';
import SojiBaseInit from '@modules/SojiBase';
import { useNavigate } from 'react-router-dom';
import { Link } from 'react-router-dom';
import { FaUserAlt } from 'react-icons/fa';

export default function Header() {
    const navigate = useNavigate();

    const logOut = async () => {
        const SOJIBase = await SojiBaseInit;
        await SOJIBase.deleteCookie();
        navigate('/', { replace: true });
        console.log('logout');
    };

    return (
        <div className="user_main_header">
            <div className="container">
                <div className="row header_wrapper">
                    <div className="col-md-6">New Document</div>
                    <div className="col-md-3">Verification Result</div>
                    <div className="col-md-3 sidebar_logo_2">
                        <Link to="/">
                            <img src={Soji} />
                        </Link>
                    </div>
                </div>
                <div className="mobile_header_wrapper">
                    <div className="action_container">
                        <img src={Soji} />
                    </div>
                    <div className="action_container" onClick={logOut}>
                        <div className="user_box">
                            <FaUserAlt size={24} />
                            Logout
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
