import React from 'react';
import Modal from '@modules/Modal';
import { useAlertState } from './useAlertState';
import { colors } from '@components/typo';
import { MaterialIcons, Ionicons } from '@expo/vector-icons';
import styled from 'styled-components';

const Container = styled.div`
    background-color: #FCFEFE;
    border-radius: 10px;
    justify-content: center;
    align-items: center;
    padding: 10px;
    z-index: 3000000;
    width: 300px;
    display: flex;
    flex-direction: column;
`;

const CloseButton = styled.button`
    padding: 10px;
    border-radius: 5px;
    margin: 10px;
    background-color: ${colors.grey};
`;

const AlertTitle = styled.div`
    font-weight: 600;
`;

const AlertDesc = styled.div`
    text-align: center;
`;

export default function Alert() {
    const alertState = useAlertState();
    const alertObject = {
        state: false,
        title: '',
        desc: '',
        type: 'error',
        onClose: () => null
    };
    const alert = alertState.get();
    return (
        <Modal visible={alert.state} onRequestClose={() => alertState.set(alertObject)}>
            <Container>
                {alert.type === 'error' ? (
                    <MaterialIcons name="error-outline" size={30} color={colors.defaultColor} />
                ) : (
                    <Ionicons
                        style={{ margin: 10 }}
                        name="shield-checkmark-sharp"
                        size={30}
                        color={colors.defaultColor}
                    />
                )}
                <AlertTitle>{alert.title}</AlertTitle>
                <AlertDesc>{alert.desc}</AlertDesc>
                <CloseButton
                    onClick={() => {
                        console.log(alert.onClose)
                        alert.onClose && alert.onClose()
                        alertState.set(alertObject);
                    }}
                >
                    <div>Dismiss</div>
                </CloseButton>
            </Container>
        </Modal>
    );
}
