import React, { lazy, useState, Suspense } from 'react';
import { Routes, Route } from 'react-router-dom';
import styled from 'styled-components';

import Sidebar from '@components/layouts/Sidebar';
import Header from '@components/layouts/Header';

// const PredictionResults = lazy(() => import('../../pages/PredictionResults'));
// const RecentDocument = lazy(() => import('../../pages/RecentDocument'));
// const SecurityUpdate = lazy(() => import('../../pages/SecurityUpdate'));



const Container = styled.div`
    height: 100vh;
    width: 100vw;
    display: flex;
`;

const Content = styled.div`
    flex: 1;
    overflow-y: auto;
    overflow-x: hidden;
    padding-top: 0px;
    height: 100vh;
    display: flex;
    flex-direction: column;
`;

const ContentWrapper = styled.div`
    display: flex;
    flex-direction: column;
    flex: 1;
    width: 100%;
    height: calc(100% - 75px);
    overflow-x: hidden;
    background: #fff;
    margin-top: 2px;
    padding: 15px;
`;

export default function Access() {
    const [collapsed, setCollapsed] = useState(false);
    const [toggled, setToggled] = useState(false);

    const handleCollapsedChange = (checked) => {
        console.log(checked);
        setCollapsed(checked);
    };

    const handleToggleSidebar = (value) => {
        setToggled(value);
    };
    return (
        <Container>
            <Sidebar
                collapsed={collapsed}
                toggled={toggled}
                handleToggleSidebar={handleToggleSidebar}
                handleCollapsedChange={handleCollapsedChange}
            />
            <Content>
                <Header />
                <ContentWrapper>
                    <Suspense fallback={<></>}>
                        <Routes>
                            <Route path="/">
                                {/* <Route path="get-prediction-results/:uniqueId" element={<PredictionResults />} />
                                <Route path="recent-document" element={<RecentDocument />} />
                                <Route path="security-update" element={<SecurityUpdate />} /> */}
                            </Route>
                        </Routes>
                    </Suspense>
                </ContentWrapper>
            </Content>
        </Container>
    );
}
